$block: 'contact';

@mixin overlay {
	@include media-min(lg) {
		@content;
	}
}

.#{$block} {
	margin-bottom: 2rem;
	position: relative;

	@include overlay {
		display: flex;
		justify-content: center;
		margin-bottom: 0;
		padding-bottom: 15vw;
		padding-top: 3rem;
	}

	@include media-min(xl) {
		padding-top: 8.5rem;
	}

	&__background-image {
		height: 100%;
		object-fit: cover;
		object-position: top;
		width: 100%;
	}

	&__background-picture {
		display: block;
		height: 175px;

		@include media-min(sm) {
			height: auto;
			width: 100%;
		}

		@include overlay {
			height: 100%;
			position: absolute;
			top: 0;
			z-index: $z-index-alpha;
		}
	}

	&__content {
		align-items: center;
		display: flex;
		justify-content: center;
		padding-bottom: 2.5rem;

		@include overlay {
			background-image: linear-gradient(180deg, #fff 0%, $blue-pale 100%);
			border-radius: 4px;
			box-shadow: 0 2px 16px 2px rgba(0, 0, 0, 0.1);
			box-sizing: border-box;
			height: 520px;
			padding-bottom: 2.5rem;
			padding-left: 2.5rem;
			padding-right: 2.5rem;
			// increased padding top 4px more than bottom/left/right
			padding-top: 2.75rem;
			width: 524px;
		}
	}

	&__content-container {
		display: flex;
		justify-content: center;

		@include overlay {
			justify-content: flex-end;
		}
	}

	&__form {
		margin: 0 auto;
		max-width: 444px;
		width: 100%;

		noscript {
			div {
				text-align: center;

				@include media-min(lg) {
					text-align: left;
				}

				p {
					margin-bottom: 0;
				}
			}
		}
	}

	&__panel-container {
		background-image: linear-gradient(-180deg, #fff 8%, #f4f8fc 100%);
		position: relative;

		@include overlay {
			background-image: none;
			width: 100%;
			z-index: $z-index-charlie;
		}
	}

	&__svg-curve {
		height: 17.083vw;
		// ^ height is svg height / width
		position: absolute;
		top: 8.125rem;
		width: 100vw;
		z-index: $z-index-alpha;

		@include media-min(sm) {
			top: calc(50vw - 5.5rem);
			// ^ 50vw is contact image height divided by width (2880/1440)
		}

		// At Large media query, form positions onto image &&
		// Curve positions to bottom of contact panel.
		@include media-min(lg) {
			top: calc(100% - 7rem);
		}

		@include media-min(xxl) {
			top: calc(100% - 12.5rem);
		}
	}
}
