$block: 'stats-panel';

.#{$block} {
	background-color: $blue-regal;
	padding: 4rem 0;

	&__row {
        align-items: center;
        display: grid;
        grid-gap: 4rem;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	}

	&__column {
		color: $white;
		line-height: 1;
		text-align: center;
	}

	&__number {
		font-size: 3.5rem;
		font-weight: $font-weight-bold;
		margin-bottom: 1.15rem;
	}

	&__desc {
		font-size: 0.875rem;
		font-weight: $font-weight-normal;
		text-transform: uppercase;
	}
}
