$block: 'intro-panel';

.#{$block} {
	padding: 2rem 0;

	@include media-min(lg) {
		padding: 3.75rem 0;
	}

	&__row {
		@include media-min(lg) {
			display: flex;
			height: 100%;
			justify-content: space-evenly;
			position: relative;
		}
	}

	&__column {
		@include media-min(lg) {
			width: 50%;
		}
	}

	&__heading {
		color: $grey-dark;
		font-size: 1.875rem;
		font-weight: $font-weight-normal;

		@include media-min(lg) {
			font-size: 3.5rem;
		}
	}

	&__content {
		color: $grey-dark;
		font-size: 1rem;
		line-height: 1.625;

		@include media-min(lg) {
			font-size: 1.125rem;
			padding-right: 15%;
		}
	}
}
