$block: 'point-list';

.#{$block} {
	display: flex;
	justify-content: center;
	margin-top: 2rem;

	@include media-min(lg) {
		display: block;

		&--row {
			margin-top: 3rem;
		}
	}

	&__list {
		list-style-type: none;
		margin: 0;
		padding: 0;
		text-align: center;

		// Overview column
		&--column {
			max-width: 275px;

			@include media-min(lg) {
				max-width: 415px;
				text-align: left;
			}
		}

		// Go-Habanero row (at desktop)
		&--row {
			max-width: 318px;

			@include media-min(lg) {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				margin-top: 3rem;
				max-width: none;
			}
		}
	}

	&__item {
		margin-bottom: 2.5rem;

		// Go-Habnero items
		&--column {
			margin-top: 0;
			text-align: center;

			&:last-of-type {
				margin-bottom: 0;
			}

			@include media-min(lg) {
				margin-bottom: 0;
				width: calc(33.333% - 20px);
			}
		}

		// Overview items
		&--row {
			&:nth-of-type(3) {
				margin-bottom: 0;
			}

			@include media-min(lg) {
				align-items: flex-start;
				display: flex;
				flex-direction: row;
				margin-bottom: 2rem;

				&:nth-of-type(3) {
					margin-bottom: 0;
				}
			}
		}
	}
}
