$block: 'text-input';

.#{$block} {
	position: relative;
	width: 100%;

	&__input {
		border-radius: 28px;
		position: relative;
		z-index: $z-index-bravo;
	}

	&__input,
	&__text-area {
		-webkit-appearance: none;
		border: 1px solid transparent;
		box-shadow: 0 1px 8px 1px rgba(0, 0, 0, 0.05);
		box-sizing: border-box;
		font-size: $font-size-medium;
		outline: none;
		padding-bottom: 1rem;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
		padding-top: 1rem;
		transition: padding 0.35s ease;
		width: 100%;

		@media (prefers-reduced-motion: reduce) {
			transition: none !important;
		}

		&:focus {
			border: 1px solid $blue;
		}

		// IE 10-11
		&:not(:-ms-input-placeholder) {
			padding-bottom: 0.5rem;
			padding-top: 1.5rem;
		}

		&:not(:placeholder-shown) {
			padding-bottom: 0.5rem;
			padding-top: 1.5rem;
		}
	}

	// IE 10-11
	&__input:not(:-ms-input-placeholder) + .text-input__label,
	&__text-area:not(:-ms-input-placeholder) + .text-input__label {
		color: $grey-dark;
		font-size: 0.6875rem;
		font-weight: $font-weight-bold;
		opacity: 0.7;
		top: 0.5rem;
		z-index: $z-index-charlie;
	}

	&__input:not(:placeholder-shown) + .text-input__label,
	&__text-area:not(:placeholder-shown) + .text-input__label {
		color: $grey-dark;
		font-size: 0.6875rem;
		font-weight: $font-weight-bold;
		opacity: 0.7;
		top: 0.5rem;
		z-index: $z-index-charlie;
	}

	&__label {
		left: 1.6rem;
		opacity: 0;
		position: absolute;
		top: 1rem;
		transition: top 0.35s ease;
		z-index: $z-index-alpha;

		@media (prefers-reduced-motion: reduce) {
			transition: none !important;
		}
	}

	&__text-area {
		border-radius: 12px;
		display: block;
		position: relative;
		resize: none;
		z-index: $z-index-bravo;
	}
}
