@import '../reference/reference';

$block: 'intro';

.#{$block} {
	padding: 3.5rem 0 1rem;

	@include media-min(lg) {
		padding: 4rem 0;
	}

	&__container {
		padding-right: 15%;
	}

	&__heading {
		font-size: 1.875rem;
		font-weight: $font-weight-normal;
		line-height: 1.25;

		@include media-min(lg) {
			font-size: 4rem;
		}
	}
}
