@import '../reference/reference';

$block: 'casestudy-panel';

.#{$block} {
	background-color: #f4f4f4;

	&__container {
		display: flex;
		flex-direction: column;
		flex-flow: column-reverse;
	}

	&__content {
		padding: 3.75rem 0;

        h1 {
            margin-top: 0;
        }
	}

    &__picture-container {
        margin-left: -1.375rem;
    }

	@include media-min(lg) {
		padding: 6rem 0;

		&__container {
			flex-direction: row;
			flex-flow: row;
		}

		&__picture-container {
			width: 60%;
		}

		&__content {
			padding: 5rem 0 5rem 5rem;

			h1 {
				margin-top: 0;
			}
		}
	}
}
