@import '../reference/reference';

$block: 'overview';

.#{$block} {
	&__column {
		box-sizing: border-box;

		@include media-min(lg) {
			margin-bottom: 0;
		}

		// Column with picture element
		&:nth-of-type(1) {
			display: flex;
			justify-content: center;
			margin-bottom: 2rem;

			@include media-min(lg) {
				flex: 1;
				margin-bottom: 0;
			}
		}

		// column with content
		&:nth-of-type(2) {
			display: flex;
			justify-content: center;

			@include media-min(lg) {
				justify-content: flex-start;
				margin-right: 4rem;
				max-width: 440px;
				width: 45%;
			}
		}
	}

	&__content {
		max-width: 40rem;
	}

	&__image {
		border-top-left-radius: 1.5rem;
		height: auto;
		max-width: 814px;
		width: 147%;

		@include media-min(sm) {
			max-width: 40rem;
			width: auto;
		}

		@include media-min(lg) {
			max-width: none;
			width: 155%;
		}
	}

	&__picture {
		@include media-min(lg) {
			margin-bottom: -6rem;
			position: relative;
			top: -6rem;
		}
	}

	&__row {
		@include media-min(lg) {
			display: flex;
			flex-direction: row-reverse;
		}
	}

	svg {
		display: block;
		width: 100%;

		@include media-min(lg) {
			height: 72px;
			width: 72px;
		}
	}
}
