@import '../reference/reference';

.point {
	&__heading {
		font-size: $font-size-medium;
		margin-bottom: 0.5rem;
		margin-top: 0;

		@include media-min(sm) {
			font-size: $font-size-large;
		}
	}

	&__content {
		> *:last-child {
			margin-bottom: 0;
			margin-top: 0;
		}
	}

	&__image-container {
		height: 49px;
		margin-bottom: 1rem;
		margin-left: auto;
		margin-right: auto;
		width: 49px;

		@include media-min(lg) {
			height: auto;
			width: auto;
		}

		&--overview {
			@include media-min(lg) {
				margin-bottom: 0;
				margin-right: 1rem;
			}
		}
	}
}
